<template>
  <div class="upload-wrap">
    <cube-upload
      v-model="value"
      :max="1"
      :auto="false"
      :multiple="false"
      accept="image/jpg, image/jpeg, image/png"
      :processFile="processFile"
      :simultaneous-uploads="1"
      @file-submitted="onFileSubmitted"
      @file-removed="onFileRemoved"
      @files-added="onFilesAdded" />
    <cube-loading class="loading" v-show="loading"></cube-loading>
  </div>
</template>

<script>
import uploadFix, { getOrientation } from '../utils/uploadFix';

export default {
  data() {
    return {
      loading: false,
    };
  },
  props: {
    value: {
      type: Array,
      default: () => [],
    },
    fileSubmitted: {
      type: Function,
      default: () => {},
    },
    fileRemoved: {
      type: Function,
      default: () => {},
    },
    filesAdded: {
      type: Function,
      default: () => {},
    },
  },
  methods: {
    onFileSubmitted(file) {
      this.loading = false;
      this.$emit('fileSubmitted', file);
    },
    onFileRemoved(file) {
      this.$emit('fileRemoved', file);
    },
    onFilesAdded(file) {
      this.loading = true;
      this.$emit('filesAdded', file);
    },
    processFile(file, next) {
      getOrientation(file).then((orientation) => {
        const needRotate = orientation && orientation !== 1;
        const reader = new FileReader();
        reader.onload = (e) => {
          if (needRotate) {
            const img = new Image();
            img.src = e.target.result;
            img.onload = () => {
              const data = uploadFix.compress(img, orientation);
              file.base64 = data;
              next(file);
            };
          } else {
            file.base64 = e.target.result;
            next(file);
          }
        };
        reader.readAsDataURL(file);
      });
    },
  },
};
</script>

<style lang="stylus" scoped>
  @import "../assets/styles/common.styl";
// 
  .upload-wrap {
    width: 98px;
    height: 98px;
    position: relative;
    >>> .cube-upload-btn-def {
      width: 98px;
      height: 98px;
      border:1px dashed $border-color2;
      border-radius:5px;
      &:after {
        border: none;
      }
    }

    >>> .cube-upload-file-def {
      width: 98px;
      height: 98px;
    }

    >>> .cube-upload-btn-def {
      background: transparent;
    }

    >>> .cube-upload-file-state {
      width: 98px;
      height: 98px;
    }

    >>> .cube-upload-btn-def > i::before,  >>> .cube-upload-btn-def > i::after{
      width: 30px;
    }

  }

  .white >>> .cube-upload-btn-def {
    border-color: $color-white;
  }
  .white >>> .cube-upload-btn-def > i::before,.white >>> .cube-upload-btn-def > i::after {
    background-color: $color-white;
  }
  .white >>> .cube-upload-file-def {
    background-color: transparent;
  }

  .loading {
    position: absolute;
    display: flex;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    justify-content: center;
    align-items: center;
  }

</style>
